@import "normalizes.scss";
@import "resets.scss";


$radius-box: 100px;
$shadow-box: 0px 4px 4px rgba(0, 0, 0, 0.25);
$color-orange: #F28B64;
$color-gray: #E2E2E2;
$color-blue: #5290EA;
$icon-padding: 12px 5px;

* {
    box-sizing: border-box; }

.container {
    max-width: 1140px;
    margin: 0 auto; }

@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-Regular.ttf ');
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-SemiBold.ttf ');
    font-weight: bold;
    font-style: normal; }

html {
    font-family: MontserratT;
    font-weight: regular; }


.main-section {
    position: relative;
    min-height: 800px;
    background: #F0F0F0;
    padding-top: 16px;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center; }
    &__logo {
        width: 134px; }
    &__content-conteiner {
        display: flex;
        flex-direction: row;
        position: relative; }
    &__firste-elipse {
        position: absolute;
        top: 150px;
        right: 50px; }
    &__second-elipse {
        position: absolute;
        top: 650px;
        right: -150px; } }


.nav_menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 64px;
        font-size: 24px;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: black; } } }

.left-wrapper {
    position: relative;
    &__img {
        position: relative;
        z-index: 2; }
    &__rectangle-blue {
        position: absolute;
        top: 150px;
        left: -50px;
        width: 565.95px;
        height: 220px;
        z-index: 0;
        background: $color-blue;
        border-radius: $radius-box;
        box-shadow: $shadow-box;
        transform: rotate(-64deg); }
    &__rectangle-orange {
        position: absolute;
        width: 618.43px;
        height: 220px;
        top: 165px;
        left: -40px;
        background: $color-orange;
        border-radius: $radius-box;
        box-shadow: $shadow-box;
        transform: rotate(-50.81deg);
        z-index: 1; }
    &__rectangle-gray {
        position: absolute;
        width: 618.43px;
        height: 220px;
        top: 200px;
        left: 10px;
        background: $color-gray;
        border-radius: $radius-box;
        box-shadow: $shadow-box;
        transform: rotate(-36.15deg);
        z-index: 0; } }


.rigth-wrapper {
    position: absolute;
    width: 372px;
    top: 150px;
    right: 56px;
    &__icon-box {
        margin-bottom: 1px;
        padding-left: 10px; }
    &__tittle {
        margin-bottom: 10px;
        z-index: 1; }
    &__text {
        font-size: 16px;
        margin-bottom: 20px;
        position: relative;
        z-index: 1; } }



.icons {
    display: inline-flex;
    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border: 1px solid black;
        border-radius: $radius-box;
        margin-right: 17px;
        &:last-child {
            margin-right: 0px; }
        i {}
        font-size: 21px;
        z-index: 1; } }


.tittle {
    font-size: 68px;
    position: relative;
    font-weight: bold;

    &__hr {
        width: 65px;
        position: absolute;
        bottom: 0px;
        margin: 0;
        border: none;
        border-top: 1px solid black;
        &_second {
            bottom: -7px; }
        &_top {
            bottom: 75px; } } }



.buttons {
    display: inline-flex;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: none;
        font-size: 18px;
        color: white; }
    &__icon-btn {
        display: inline-flex;
        align-items: center; }
    &__icon-chat {
        margin-right: 12px; }
    &__text {
        font-size: 16px; } }


.elipse {
    width: 194px;
    height: 194px;
    background: linear-gradient(143.92deg, rgba(237, 123, 80, 0.5) 14.85%, rgba(72, 138, 234, 0.5) 53.25%, rgba(44, 44, 44, 0.5) 85.74%);
    border-radius: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    &_big {
        width: 282px;
        height: 282px; }
    &__subtract {
        width: 129.33px;
        height: 129.33px;
        background: #F0F0F0;
        border-radius: 200px;
        &_big {
            width: 188px;
            height: 188px; } } }


