.second-section {
    min-height: 700px;
    overflow: hidden;
    &__container {
        padding-top: 32px;
        position: relative; }
    &__tittle {
        margin-bottom: 40px; }
    &__card-box {
        display: inline-flex;
        margin-bottom: 25px; }
    &__cards-item {
        margin-right: 30px;
        &:last-child {
            margin-right: 0px; } }
    &__icons-box {
        display: flex;
        justify-content: space-evenly; }
    &__icons-item {
        &:last-child {
            margin-right: 0px; } }
    &__firste-elipse {
        position: absolute;
        top: 80px;
        right: -60px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        left: -60px;
        top: 364px;
        z-index: -1; } }

.cards-item {
    position: relative;
    width: 360px;
    height: 360px;
    background: #E2E2E2;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;
    &__img {
        height: 335px; }
    &__text {
        width: 280px;
        font-size: 36px;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 144px;
        right: -30px;
        font-weight: bold; }
    &_orange {
        background: $color-orange; }
    &_blue {
        background: $color-blue; } }

.icons-item {
    display: inline-flex;
    align-items: center;
    &__img {
        margin-right: 20px;
        height: 80px;
        width: 80px; }
    &__text {
        font-size: 18px;
        span {
            font-size: 48px;
            font-weight: bold; } } }


.third-section {
    min-height: 640px;
    background-color: $color-gray;
    z-index: 0;
    position: relative;
    overflow: hidden;
    &__container {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-left: 167px;
        padding-top: 46px; }
    &__right-wrapper {
        max-width: 480px; }
    &__right-tittle {
        margin-bottom: 20px; }
    &__text {
        font-size: 18px;
        margin-bottom:  30px; }
    &__first-elipse {
        position: absolute;
        left: 140px;
        top: 110px;
        z-index: -1; }
    &__figure {
        position: absolute;
        top: 249px;
        left: 15px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        top: -120px;
        right: -120px; } }

.footer {
    &__tittle {
        margin-bottom: 60px; }
    &__container {
        min-height: 272px;
        display: flex;
        align-items: center;
        justify-content: flex-end; } }

@media (max-width: 1199px) {
    .container {
        width: 960px; }
    .main-section {
        min-height: 690px; }
    .left-wrapper {
        img {
            width: 750px; }

        &__rectangle-blue {
            width: 500px;
            height: 200px; }
        &__rectangle-orange {
            width: 550px;
            height: 200px; }
        &__rectangle-grey {
            width: 500px;
            height: 200px; } }
    .right-wrapper {
        right: 20px; }
    .main-section__first-elipse {
        right: 10px; }
    .main-section__second-elipse {
        top: 560px; }

    .second-section {
        min-height: 600px;
        &__cards-box {
            display: flex;
            justify-content: center; } }
    .cards-item {
        width: 300px;
        height: 300px;
        margin-right: 20px; }
    .cards-item__img {
        height: 270px; }
    .cards-item__text {
        top: 120px;
        font-size: 30px; }
    .icons-item__img {
        margin-right: 10px; }

    .third-section__container {
        padding-left: 40px; }
    .third-section__right-wrapper {
        max-width: 450px; } }

@media (max-width: 991px) {
    .main-section {
        min-height: 530px;
        &__first-elipse {
            top: 70px; }
        &__second-elipse {
            top: 400px; } }
    .container {
        width: 720px; }
    .left-wrapper {
        img {
            width: 550px; } }
    .left-wrapper {
        &__rectangle-blue {
            width: 380px;
            height: 130px; }
        &__rectangle-orange {
            width: 410px;
            height: 130px; }
        .left-wrapper__rectangle-grey {
            width: 380px;
            height: 130px; } }
    .title {
        font-size: 60px; }
    .right-wrapper {
        top: 75px;
        width: 300px;
        &__text {
            margin-bottom: 10px;
            font-size: 14px; } }
    .buttons {
        &__main-btn {
            font-size: 14px;
            margin-right: 10px;
            width: 160px;
            height: 50px; }
        &__icon-btn {
            font-size: 14px; } }
    .elipse {
        width: 150px;
        height: 150px;
        &__subtract {
            width: 110px;
            height: 110px; }
        &__subtract_big {
            width: 170px;
            height: 170px; } }

    .second-section {
        min-height: 450px;
        &__container {
            padding-top: 20px; }
        &__title {
            margin-bottom: 25px; }
        &__cards-box {
            margin-bottom: 10px; }
        &__first-elipse {
            right: -35px; }
        &__second-elipse {
            top: 262px;
            left: -35px; } }

    .cards-item {
        width: 220px;
        height: 220px;
        &__img {
            height: 200px; }
        &__text {
            font-size: 22px;
            top: 92px;
            right: -75px; } }
    .icons-item {
        &__img {
            width: 50px; }
        &__text {
            font-size: 14px;
            span {
                font-size: 30px; } } }

    .elipse_dig {
        width: 250px;
        height: 250px; }

    .third-section {
        min-height: 510px;
        &__left-title {
            display: none; }
        &__right-wrapper {
            max-width: 600px; }
        &__container {
            justify-content: flex-start;
            padding-left: 20px;
            padding-top: 20px; }
        &__right-title {
            margin-bottom: 10px; } }

    .title {
        &__hr_top {
            bottom: 60px; } }

    .footer {
        &__img {
            width: 500px; }
        &__title {
            margin-bottom: 10px; }
        &__container {
            min-height: 210px; } } }




@media (max-width: 767px) {
    .container {
        width: 540px; }
    .nav_menu {
        position: fixed;
        right: -100%;
        top: 0px;
        height: 100vh;
        width: 50%;
        background: $color-blue;
        z-index: 10;
        transition: 1s all;
        &__list {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            height: 100%; }
        &__list-item {
            margin-right: 0px;
            font-size: 20px; } }
    .hamburger {
        width: 30px;
        z-index: 100;
        position: fixed;
        right: 25 px;
        top: 25px;
        span {
            height: 2px;
            width: 100%;
            margin-bottom: 4px;
            display: block;
            background: black;
            transition: 1s all;
            &:last-child {
                margin-bottom: 0px; } } }

    .hamburger_active {
        span {
            margin-bottom: -2px;
            &:nth-child(1) {
                transform: translateY(3px) rotate(-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                transform: translateY(3px)  rotate(45deg); } } }

    .nav-menu_active {
        display: block;
        right: 0px; } }


@media (max-width: 575px) {
    .container {
        width: 100%; } }




